@import "settings";

.rich-content {
  blockquote {
    margin: 0;
    padding: 5px 5px 5px 40px;
    border-left: 5px solid $color-secondary;
  }

  img {
    display: block;
    margin: auto;
  }
}

.card {
  .rich-content {
    img {
      max-width: 250px;
    }
  }
}


