@import "settings";

.button {
  display: inline-block;
  border: none;
  font-size: 15px;
  padding: 15px;
  border-radius: 2px;
  cursor: pointer;
  transition: all ease-in-out $default-transition-duration;
  outline: none;
  text-decoration: none;
  margin: 5px;

  &.icon {
    width: 35px;
    height: 35px;
    border-radius: 50px;
    padding: 5px;
  }

  &.primary {
    &.flat {
      background-color: transparent;
      color: $color-primary;
    }
    &:not(.flat) {
      background-color: $color-primary;
      color: $color-primary-reverse;

      &:hover:not([disabled]) {
        background-color: $color-primary-hover;
      }

      &[disabled] {
        color: $color-disabled;
        cursor: not-allowed;
      }
    }
  }

  &.secondary {
    &.flat {
      background-color: transparent;
      color: $color-secondary;

      &:hover:not([disabled]) {
        color: $color-secondary-hover;
      }
    }
    &:not(.flat) {
      background-color: $color-secondary;
      color: $color-secondary-reverse;

      &:hover:not([disabled]) {
        background-color: $color-secondary-hover;
      }

      &[disabled] {
        color: $color-disabled;
        cursor: not-allowed;
      }
    }
  }

  &.delete:not(.action) {
    &.flat {
      background-color: transparent;
      color: $color-delete;
    }
    &:not(.flat) {
      background-color: $color-delete;
      color: $color-delete-reverse;

      &:hover:not([disabled]) {
        background-color: $color-delete-hover;
      }

      &[disabled] {
        color: $color-disabled;
        cursor: not-allowed;
      }
    }
  }

  &.action {
    background-color: transparent;
    padding: 15px;
    text-transform: uppercase;
    font-weight: 600;

    &:not(.delete):not(.primary) {
      color: $color-action;

      &:hover {
        color: $color-action-hover;
      }
    }

    &.delete {
      color: $color-action-delete;

      &:hover {
        color: $color-action-delete-hover;
      }
    }
  }
}

a:not(.button) {
  color: $color-primary;
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}


